<template>
  <div id="contact">
    <el-tabs v-model="activeName" @tab-click="handleClick" type="border-card">
      <el-tab-pane label="方案1" name="first">
        <type-a></type-a>
      </el-tab-pane>
      <el-tab-pane label="方案2" name="second">
        <type-b></type-b>
      </el-tab-pane>
    </el-tabs>
    <div class="history">
      <h2>历史记录</h2>
      <el-table
      :data="historyList"
      stripe
      style="width: 100%">
      <el-table-column
        prop="szcyl"
        label="上周车源量(s)"
        width="80">
      </el-table-column>
      <el-table-column
        prop="cyjg"
        label="车源价格(p0)"
        width="95">
      </el-table-column>
      <el-table-column
        prop="szcyzdj"
        label="上周车源最低价(p1)"
        width="95">
      </el-table-column>
      <el-table-column
        prop="szcyzgj"
        label="上周车源最高价(p2)"
        width="95">
      </el-table-column>
      <el-table-column
        prop="clrq"
        label="车辆日期"
        width="95">
      </el-table-column>
      <el-table-column
        prop="pjlc"
        label="车源年平均里程(m0)"
        width="95">
      </el-table-column>
      <el-table-column
        prop="szcyzdlc"
        label="上周车源最低里程(m1)"
        width="95">
      </el-table-column>
      <el-table-column
        prop="szcyzglc"
        label="上周车源最低里程(m2)"
        width="95">
      </el-table-column>
      <el-table-column
        prop="scoreA"
        label="得分A"
        width="95">
      </el-table-column>
      <el-table-column
        prop="scoreB"
        label="得分B"
        width="95">
      </el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="95">
        <template slot-scope="scope">
          <el-button @click="handleUse(scope)" type="text" size="small">使用</el-button>
        </template>
      </el-table-column>
    </el-table>
    </div>
  </div>
</template>

<script>
import {mapState,mapActions} from 'vuex'
import { Tabs, TabPane, Table, TableColumn, Button} from 'element-ui';
import typeA from '../component/typeA.vue'
import typeB from '../component/typeB.vue'
export default {
  name:"contact",
  computed: {
    ...mapState(["historyList"])
  },
  components:{
      [Tabs.name]:Tabs,
      [TabPane.name]:TabPane,
      [Table.name]:Table,
      [TableColumn.name]:TableColumn,
      [Button.name]:Button,
      'type-a':typeA,
      'type-b':typeB
    },
  data() {
    return {
      activeName: 'first',
    };
  },
  methods: {
    ...mapActions(['getHistory']),
    handleClick(tab, event) {
      console.log(tab, event);
    },
    handleUse(res){
      this.getHistory(res.row)
      this.$bus.$emit('useItem1',res.row)
    }
  }
}
</script>

<style>
#contact{
  display: flex;
}
.el-tabs{
  width: 50%;
}
.history{
  width: 50%;
}
.el-table{
  width: 50%;
  /* float: right; */
}
h2{
  text-align: center;
  margin-bottom: 20px;
}
</style>



