<template>
<div class="wrap">
  <el-form :inline="false" class="demo-form-inline">
    <el-form-item label="上周车源量(s)">
      <el-input v-model="szcyl" placeholder="上周车源量"></el-input>
    </el-form-item>
    <el-form-item label="车源价格(p0)">
      <el-input v-model="cyjg" placeholder="车源价格"></el-input>
    </el-form-item>
    <el-form-item label="上周车源最低价(p1)">
      <el-input v-model="szcyzdj" placeholder="上周车源最低价"></el-input>
    </el-form-item>
    <el-form-item label="上周车源最高价(p2)">
      <el-input v-model="szcyzgj" placeholder="上周车源最高价"></el-input>
    </el-form-item>
    <el-form-item label="车辆日期">
      <el-date-picker
        v-model="clrq"
        value-format="yyyy-MM-dd"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期">
      </el-date-picker>
    </el-form-item>
    <el-form-item label="车源年均里程(m0)">
      <el-input v-model="cllc" placeholder="车源年均里程"></el-input>
    </el-form-item>
    <el-form-item label="上周车源最低里程(m1)">
      <el-input v-model="szcyzdlc" placeholder="上周车源年均最低里程"></el-input>
    </el-form-item>
    <el-form-item label="上周车源最高里程(m2)">
      <el-input v-model="szcyzglc" placeholder="上周车源年均最高里程"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="success" @click="onSubmit">查询</el-button>
      <el-button type="primary" @click="deleteAll">清空</el-button>
    </el-form-item>
  </el-form>
  <el-table
    v-if="tableData"
    :data="tableData"
    style="width: 100%">
    <el-table-column
      prop="cl"
      label="车龄"
      width="180">
    </el-table-column>
    <el-table-column
      prop="scoreA"
      label="得分A"
      width="180">
    </el-table-column>
    <el-table-column
      prop="scoreB"
      label="得分B">
    </el-table-column>
  </el-table>
  <el-button :plain="true" @click="onSubmit"></el-button>
</div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { Form, FormItem, Button, Input,Select,Option,DatePicker,Table,TableColumn} from 'element-ui';
export default {
  name:'typeA',
  components:{
      [Form.name]:Form,
      [FormItem.name]:FormItem,
      [Button.name]:Button,
      [Input.name]:Input,
      [Select.name]:Select,
      [Option.name]:Option,
      [DatePicker.name]:DatePicker,
      [Table.name]:Table,
      [TableColumn.name]:TableColumn,
  },
   computed: {
    ...mapState(["useList"])

  },
   data() {
      return {
        szcyl:'',//上周车源量
        cyjg:'',//车源价格
        szcyzdj:'',//上周车源最低价
        szcyzgj:'',//上周车源最高价
        clrq:'',//车辆日期
        cl:'',//车龄
        pjlc:'',//平均里程
        cllc:'',//车辆里程
        tableData:[],
        szcyzdlc:'',//上周车源最低里程
        szcyzglc:'',//上周车源最高里程
        // szcypjzdlc:'',//上周车源平均最低里程
        scoreA:'',//得分A
        scoreB:'',//得分B
        scoreC:'',//得分C
      }
    },
    mounted(){
      this.$bus.$on('useItem1',(obj)=>{
        this.useItem(obj)
      })
    },
    methods: {
      ...mapActions(["setHistory"]),
      onSubmit() {
        if(!Array.isArray(this.clrq)){
          this.$message.error('日期必须填写！');
          return
        }else{
          this.cl=this.fn1(this.clrq[0],this.clrq[1])
        }
        if(!(this.szcyl && this.cyjg && this.szcyzdj && this.szcyzgj)){
          this.$message.error('有忘记填写的数据！');
          return
        }
        this.pjlc=this.cllc/this.cl
        this.scoreA = 100-(60/this.szcyl)*(this.cyjg-this.szcyzdj)/((this.szcyzgj-this.szcyzdj)/this.szcyl)
        this.scoreB = 100-(60/this.szcyl)*(this.pjlc-this.szcyzdlc)/((this.szcyzglc-this.szcyzdlc)/this.szcyl)
        this.tableData.push({'cl':this.cl},{'scoreA':this.scoreA},{'scoreB':this.scoreB})
        this.setHistory({
          szcyl:this.szcyl,
          cyjg:this.cyjg,
          szcyzdj:this.szcyzdj,
          szcyzgj:this.szcyzgj,
          clrq:this.clrq,
          pjlc:this.pjlc,
          szcyzdlc:this.szcyzdlc,
          szcyzglc:this.szcyzglc,
          scoreA:this.scoreA,
          scoreB:this.scoreB,
        })
      },
      deleteAll(){
        this.szcyl=''//上周车源量
        this.cyjg=''//车源价格
        this.szcyzdj=''//上周车源最低价
        this.szcyzgj=''//上周车源最高价
        this.clrq=''//车辆日期
        this.cl=''//车龄
        this.pjlc=''//平均里程
        this.cllc=''//车辆里程
        this.tableData=[]
        this.szcyzdlc=''//上周车源最低里程
        this.szcyzglc=''//上周车源最高里程
        // this.szcypjzdlc=''//上周车源平均最低里程
        this.scoreA=''//得分A
        this.scoreB=''//得分B
        this.scoreC=''//得分C
      },
      fn1(sDate1, sDate2){
        let  aDate,  oDate1,  oDate2,  iDays
        aDate   =  sDate1.split("-")
        oDate1 = new Date(aDate[0] , aDate[1] ,aDate[2])
        aDate   =  sDate2.split("-")
        oDate2 = new Date(aDate[0] , aDate[1] , aDate[2])
        iDays   =  parseFloat(Math.abs(oDate1  -  oDate2)  /  1000  /  60  /  60  /24 /365)
        return  iDays
      },
      useItem(obj){
        this.szcyl=obj.szcyl
        this.cyjg=obj.cyjg
        this.szcyzdj=obj.szcyzdj
        this.szcyzgj=obj.szcyzgj
        this.clrq=obj.clrq
        this.pjlc=obj.pjlc
        this.szcyzdlc=obj.szcyzdlc
        this.szcyzglc=obj.szcyzglc
        this.scoreA=obj.scoreA
        this.scoreB=obj.scoreB
      }
    },
    computed:{
      // 车龄
      // cl:this.fn1(this.clrq[0],this.clrq[1])
    }
}
</script>

<style>

</style>



